.country_highlight_row.itinery_hightlight_row{
    .country_highlight_inr{
        p{
            span{
                color: $primary-color;
                text-transform: uppercase;
                margin-right: 5px;
            }
        }
    }
    .itinery_highlight_inr{
        ul{
            padding-left: 20px;
            border-left: 1px solid $white-color;
            li{
                font: 400 1rem/1.5rem $Roboto-Regular;
                color: $white-color;
                &:first-child{
                    text-transform: uppercase;
                    color: $primary-color;
                    margin-bottom: 5px;
                }
            }
        }
    }
}
.itinery_detls_row{
    background: $light-dark-grey;
    padding: 50px 0;
    h3{        
        font: 2.5rem/2.875rem $Slabo;
        color: #8aad56;
        text-align: center;
        padding: 0 0 30px;
        text-transform: uppercase;
    }    
}
.best_time_blk_row{
    background: $light-grey;
    padding: 50px 0;
    .best_time_blk_inr{
        padding: 20px;
        border: 10px solid #535152;
        margin-bottom: 50px;
        h3{
            font: 1.875rem/2.25rem $Slabo;
            color: #8aad56;
            text-align: left;
            padding: 0 0 10px;
            text-transform: uppercase;
            border-bottom: 2px solid #535152;
        }
    }
}
.map_blk_row{
    h3{        
        font: 2.5rem/2.875rem $Slabo;
        color: #8aad56;
        text-align: center;
        padding: 20px 0 30px;
        text-transform: uppercase;
    }  
    P{
        font: 400 1rem/1.375rem $Roboto-Regular;
        color: $white-color;
        text-align: center;
        padding-bottom: 30px;
    }  
    .map_blk_inr{
        iframe{
            width: 100%;
            height: 450px;
        }
    }
}

.best_time_blk_left{
    margin-top: 20px;
    li{
        font: 300 0.75rem/0.9375rem $Roboto-Light;
        color: $white-color;
        padding-bottom: 5px;
        display: flex;
        align-items: center;
        span{
            width: 40px;
            height: 7px;
            margin-right: 10px;
            &.shade01{
                background-color: $primary-color;
            }
            &.shade02{
                background-color: #c3de9a;
            }
            &.shade03{
                background-color: #e9fccc;
            }
            &.shade04{
                background-color: $white-color;
            }
        }
    }
}

.itinery_detls_cntnt{
    margin-bottom: 25px;
    &:last-child{
        margin-bottom: 0;
    }
    .itinery_detls_para{
        background: #3e3c3d;
        padding: 20px;
        height: 100%;
        h3{
            font: 1.5rem/1.75rem $Slabo;
            text-align: left;
            padding: 0;
            margin-bottom: 10px;
            span{
                color: $white-color;
                margin-right: 10px;
            }
        }
        p{
            font: 300 1rem/1.375rem $Roboto-Light;
            margin-bottom: 10px;
            color: $white-color;
            a{
                color: $primary-color;
                text-decoration:none;
            }
            &:nth-child(3){
                margin-bottom: 0;
            }
        }
        button{
            color: $white-color;
            font: 300 0.75rem/0.9375rem $Roboto-Light;
            text-transform: uppercase;
            display: block;
            margin: 5px auto 0;
            svg{                    
                width: 20px;
                display: block;
                margin: 0 auto;
                &.up_arrow{
                    margin: 0 auto;
                }
            }
            &.read_more{
                span{
                    display: none;
                    &.read_less{
                        display: block;
                    }
                }
                svg{
                    &.up_arrow{
                        transform: rotate(180deg);
                        margin: 0 auto;
                    }
                }
                
            }
            span{
                &.read_less{
                    display: none;
                }
            }
        }
    }
    .itinery_detls_img{
        background: #3e3c3d;
        height: 100%;
        display: flex;
        img{
            width: 100%;
            max-height: 230px;
            margin: auto;
        }
    }
}

.best_time_blk_right{
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    align-items: center;
    li{
        font: 1.125rem/1.5rem $Slabo;
        color: $white-color;
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-right: 8px;
        text-transform: uppercase;
        span{
            width: 40px;
            height: 7px;
            margin-top: 5px;
            &.shade01{
                background-color: $primary-color;
            }
            &.shade02{
                background-color: #c3de9a;
            }
            &.shade03{
                background-color: #e9fccc;
            }
            &.shade04{
                background-color: $white-color;
            }
        }
    }
}

.search_result_title{
    font: 2.25rem/2.5rem $Slabo;
    color: $white-color;
    text-align: center;
}
.title_cls.search_result_title_green{
    font: 2rem/2.25rem $Slabo;
    text-transform: unset;
}
// SOF Country, property and month dropdown scss
    // font: 400 0.875rem/1rem $Roboto-Regular;
.select__control{
    background-color: transparent !important;
    border-radius: 0 !important;
    border-color: #fff !important;
    color: #fff !important;
    height: 40px !important;
}

.select__control{
    min-height: 40px;
}
.select__placeholder {
    color: #fff;
}

.select__value-container{
    padding: 0 0 0 5px;
    display: flex;
}
.select__multi-value__label{
    font-size: 16px !important;
    line-height: 24px !important;
    color: #fff !important;
    padding: 0 !important;
}

.select__indicator.select__clear-indicator, .select__input-container  {
    display: none;
}

.select__multi-value:nth-child(1){
    background: transparent;
    color: #fff;
    display: flex;
    align-items: center;
    padding: 2px 0;
    margin-right: 5px;
    border-radius: 4px;
}
.select__multi-value:nth-child(1) .select__multi-value__label + .select__multi-value__remove{
    display: none;
}
.select__multi-value__label{
    color: #fff;
}

.select__control{
    background: transparent;
    border-radius: 0;
    border-color: #fff;
    color: #fff;
    height: 40px;
}

.select__control{
    min-height: 40px;
}
.select__placeholder {
    color: #fff;
}

.select__value-container{
    padding: 0 0 0 5px !important;
    display: flex !important;
    width: calc(100% - 37px) !important;
    overflow: hidden !important;
    flex-wrap: nowrap !important;
}
.select__multi-value__label{
    font-size: 16px !important;
    color: #fff !important;
    line-height: 28px;
}

.select__indicator.select__clear-indicator, .select__input-container, .select__indicator-separator{
    display: none;
}

.select__multi-value:nth-child(n+2){
    background: #8aad56;
    color: #fff;
    display: flex;
    align-items: center;
    padding: 2px 4px;
    margin: 0;
    margin-right: 5px;
    border-radius: 4px;
    min-width: auto !important;
}
.select__multi-value__remove{
    padding: 0 5px;
}
.select__menu {
    margin: 0 !important;
    border-radius: 0 !important;
    background: #333132 !important;
}
.select__option{
    font-size: 16px !important;
    padding: 8px 10px !important;
    color: #fff !important;
    cursor: pointer !important;
}
.select__option:hover, .select__option:focus, .select__option:active, .select__option--is-focused {
    background-color: #8aad56 !important;
    color: #000 !important;
}
.select__indicators{
    background: #333132 !important;
    cursor: pointer !important;
}

.dropdown_grp_blk{
    width: calc(100% - 160px);
    float: left;
    margin-left: -10px;
    // display: flex;
}

.destination_dropdwn_row .dropdown_grp_blk .banner_dropdwn_blk{
    padding-bottom: 15px;
    width: calc(100% - 66.7%);
    max-width: 100%;
    float: left;
    // flex: 1;
}
.destination_dropdwn_row .dropdown_grp_blk .banner_dropdwn_blk:nth-child(n+2){
    padding-left: 10px;
}
.select__dropdown-indicator{
    position: relative;
    width: 37px;
    padding: 0;
    height: 38px;
}
.select__dropdown-indicator > svg{
    display: none;
}
.select__dropdown-indicator::after{
    background: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' shape-rendering='geometricPrecision' text-rendering='geometricPrecision' image-rendering='optimizeQuality' fill-rule='evenodd' clip-rule='evenodd' fill='white' width='20' viewBox='0 0 512 266.77'%3e%3cpath fill-rule='nonzero' d='M493.12 3.22c4.3-4.27 11.3-4.3 15.62-.04a10.85 10.85 0 0 1 .05 15.46L263.83 263.55c-4.3 4.28-11.3 4.3-15.63.05L3.21 18.64a10.85 10.85 0 0 1 .05-15.46c4.32-4.26 11.32-4.23 15.62.04L255.99 240.3 493.12 3.22z'/%3e%3c/svg%3e") no-repeat !important;
    border: none;
    right: 0;
    top: 0;
    position: absolute;
    content: "";
    width: 20px;
    background-size: 20px !important;
    height: 11px;
    border-bottom: none !important;
    margin: auto;
    border-left: none !important;
    left: 0;
    bottom: 0;
}
.destination_dropdwn_row .dropdown_grp_blk + .banner_inspire_btn{
    min-width: 170px;
}
