// SOF New Country, property and month dropdown scss

.select_container_country {
    background: transparent;
    border-radius: 0;
    border-color: #fff;
    color: #fff;
    height: 40px;
    width: 100%;
  }
  
  .select_country__control {
    width: 100% !important;
    background-color: transparent !important;
    border-radius: 0 !important;
    height: 100%;
    border-color: #fff !important;
  }
  
  .select_country__value-container {
    height: 40px;
    padding: 0 5px;
  }
  
  .select_country__placeholder {
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    color: #fff !important;
  }
  
  .select_country__placeholder+input {
    display: none;
  }
  
  .select_country__dropdown-indicator {
    position: relative;
    width: 30px;
    padding: 0;
    height: 100%;
  }
  
  .select_country__dropdown-indicator svg,
  .select_country__indicator-separator {
    display: none;
  }
  
  .select_country__dropdown-indicator::after {
    background: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' shape-rendering='geometricPrecision' text-rendering='geometricPrecision' image-rendering='optimizeQuality' fill-rule='evenodd' clip-rule='evenodd' fill='white' width='20' viewBox='0 0 512 266.77'%3e%3cpath fill-rule='nonzero' d='M493.12 3.22c4.3-4.27 11.3-4.3 15.62-.04a10.85 10.85 0 0 1 .05 15.46L263.83 263.55c-4.3 4.28-11.3 4.3-15.63.05L3.21 18.64a10.85 10.85 0 0 1 .05-15.46c4.32-4.26 11.32-4.23 15.62.04L255.99 240.3 493.12 3.22z'/%3e%3c/svg%3e") no-repeat !important;
    border: none;
    right: 0;
    top: 0;
    position: absolute;
    content: "";
    width: 20px;
    background-size: 20px !important;
    height: 11px;
    border-bottom: none !important;
    margin: auto;
    border-left: none !important;
    left: 0;
    bottom: 0;
  }
  
  .select_country__menu {
    width: 100% !important;
    margin: 0px !important;
    border-radius: 0 !important;
    background-color: #333132 !important;
  }
  
  .select_country__option {
    border-bottom: 1px solid #4d4d4d;
    padding: 8px 5px !important;
    cursor: pointer !important;
  }
  
  .select_country__option.select_country__option--is-focused {
    background-color: #8aad56 !important;
  }
  
  .select_country__option input[type=checkbox] {
    margin-right: 10px;
    accent-color: #8aad56;
  }
  
  .select_country__value-container--has-value>span {
    line-height: 38px;
  }

  .select_country__input-container input:focus, .select_country__input-container input:focus-visible {
    box-shadow: none !important;
}
