.tour_landing_parnt{
    padding: 30px 0;
    background: $light-grey;
    .tour_landing_inr{
        position: relative;
        .tab_tilte{
            font: 2.5rem/2.8125rem $Slabo;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: $primary-color;
            margin: 0;        
            padding-bottom: 30px;
            text-align: center;
        }
        p{
            font: 400 1rem/1.5rem $Roboto-Regular;
            color: $white-color;
            text-align: center;
            margin-bottom: 20px;
            a{
                color: $primary-color;
                text-decoration: none;
            }
        }
    }
}

.tour_detail_parnt{
    background-color: $light-dark-grey;
    padding: 100px 0;
    .card_slider{
        background-color: $light-grey;
        border: 1px solid #696565;
    }
    .card_slider_cnt{
        text-align: center;
        padding: 0 5px;
        ul{
            li{
                text-transform: uppercase;
                &:first-child{
                    margin-bottom: 15px;
                    text-transform: none;
                }
            }
        }
    }
    &.favrites_blk_row .card_slider_row .carousel00 .card_slider_btn{
        justify-content: space-around;
        background-color: $primary-color;
        border: 1px solid $white-color;
        &:hover, &:focus, &:active{
            background-color: #779e3e !important;
            color: $font-color;
            span{
                color: $font-color;
            }
        }
    }
}

.tour_detls_row{
    .itinery_detls_para{
        h3{
            span{
                padding-right: 10px;
                border-right: 1px solid $white-color;
            }
        }
        p{
            &:nth-of-type(1){
                padding: 15px 0 25px;
                margin: 0;
            }
            span{
                color: $primary-color;
            }
        }
    }
    .itinery_detls_cntnt .itinery_detls_img img{
        max-height: 295px;
    }
}

.make_enquiry_grn_blk{
    padding: 0 0 25px;
    .make_enquiry_grn_blk_inr{
        padding: 20px 50px;
        background: $primary-color;
        color: $font-color;
        text-align: center;
        h3{
            font: 1.5rem/1.875rem $Slabo;
            margin-bottom: 10px;
            color: $white-color;
            padding-bottom: 10px;
        }
        p{
            font: 400 1rem/1.375rem $Roboto-Regular;
            margin-bottom: 10px;
            color: $white-color;
        }
        .btn_grp{
            font: 1.5rem/2.1875rem $Slabo;
            button{
                border: 1px solid $white-color;
                background-color: $dark-grey;
                display: inline-block;
                padding: 0 30px;
                font: 400 0.875rem/0.875rem $Roboto-Regular;
            }
        }

    }
}

.dates_grey_parnt_blk{
    padding: 35px 0 0;
    .dates_grey_inr{
        background-color: #c8c7c8;
        padding: 30px;
    }
    .row{
        div{
            &:first-child{
                .date_list_parnt{
                    border-right: 1px solid $font-color;
                }
            }
        }
    }
    .date_list_parnt{
        padding-bottom: 15px;
        
        h2{
            margin-bottom: 10px;
        }
        .date_list_inr{
            // display: flex;
            justify-content: space-evenly;
        }
    }
}